      "<p>Today I will teach you how to install the Content Management System of WordPress in Cpanel.&nbsp;</p><br/><h2>Before Installing WordPress</h2><p> Before getting started, you should have purchased or subscribed already to hosting. I like to use Namecheap for buying domains and hosting since they have excellent customer service, among other great benefits. Another essential thing to note before installing WordPress is checking that your web hosting provider fulfills the necessary software and conditions. Also, you must have access to the server and some tools. So the requirements on the server side are PHP 7.4 or greater and having MySQL 5.6 or MariaDB 10.1 or greater. I&apos;m going to leave a link to a detailed list of requirements in the description of this video (1)&nbsp;</p><br/><h2>Downloading WordPress</h2><p> The first step is to go to WordPress.org and download the WordPress package. So let&apos;s open a web browser; I like to use Google Chrome. Then let&apos;s type WordPress.org/download and click enter. Where it says Download and Install yourself, you will see a button to Download Version 6.0.1 as of now, but you may see a newer version of WordPress while watching this video. Once the download has finished, the next step is to open your hosting account.</p><br/><h2>Open your Shared or Reseller Hosting Account Cpanel</h2><p> We will open the Cpanel of our hosting account, click on File Manager, and upload the zipped folder we downloaded from WordPress.org into your desired folder. You can upload the zipped folder into your Cpanel account&apos;s root installation, a subfolder, or subdomain. Once you have finished uploading the zipped WordPress package through Cpanel, it&apos;s time to extract the zipped folder into the root of where you want the WordPress files and directories to be. In my case, I&apos;m going to extract the zipped folder into a directory called &quot;new-wordpress.&quot; As you can see, the zipped WordPress folder got extracted into a folder called WordPress, and we don&apos;t want that. So let&apos;s first delete the zipped folder and then move the files and folders from the WordPress folder into the root of the folder named &quot;new-wordpress .&quot;I go into the directory, select all the files and folders and move them into the directory called &quot;new-wordpress&quot; by dragging and dropping them. Now that we have successfully uploaded and unzipped the WordPress directory, it&apos;s time to create a MySQL database, create a user, and assign it to that database.&nbsp;</p><br/><h2>Create a MySQL Database and Database User</h2><p> We create a MySQL database in CPanel by opening CPanel, going to the Databases section, and then clicking on the MySQL Databases link. On this page, we can create a new database, modify them, see your current databases, create a new MySQL user, and assign a user to a database. For this video chapter, please write down the database prefix, database name, username, and password assigned to the username.&nbsp;</p><br/><p> First, we will create a new database that will always have a prefix assigned automatically by CPanel. Then I will write a database name after calling it new underscore WordPress (new_wordpress) and click on the Create Database button. The second step is to create a new user by going to the Add New User section. I&apos;m going to call this new user &quot;user_one.&quot; We need to assign a password by clicking on the password generator button, which will open a popup to generate a new password. By clicking the generate password button, if you want a different password, you can click this button many times. Then we click the checkbox, &quot;I have copied this password in a safe place .&quot; Please make sure to write down the password or type it somewhere safe. I would recommend using LastPass to store all your passwords which I have left a link to in the description of this video. We then click on the Use Password button as it becomes available to click now. After adding the password that we just generated, you can write your own as well, but I would recommend using a tool to develop a strong password that is very difficult to figure out. LastPass also has a tool that will generate strong passwords, which you can use whenever you want to create an account.&nbsp;</p><br/><p> Now that we have created a database and a username with its password, it&apos;s time to assign the username to the newly created database by going to the Add User to Database section, selecting the new username, selecting the new database, and then clicking on the Add button. It will take us to the Manage User Privileges page, where we can assign the privileges we want this username to have for this newly created database. In this case, I will click on the All Privileges checkbox. Explaining what each privilege does would take a whole video, so for time&apos;s sake, I&apos;m going to click on the All Privileges checkbox for now. I will leave a link to the official Privileges Provided by MySQL page for your reference. We will then click on the Make Changes button and return to the MySQL Databases page. The last step is to type and run the WordPress install script by going to the URL or WordPress directory where we want WordPress installed, which in my case is https://cyberhomelife.com/new-wordpress.&nbsp;</p><br/><h2>Run the WordPress Install Script</h2><p> The install script will first ask you to pick the language you would like your WordPress website to use. After that, it lets you know that you need to know the following information. We click on the button that says Let&apos;s Go, and then the install script asks you to enter the database details we wrote down, so let&apos;s enter them into their respective field. For the Database Host, you can leave it as saying localhost. We then click on the Submit button and run the installation. It will then take us to a page asking us to provide details for the newly created WordPress website. For the Site Title, I will enter &quot;new underscore WordPress.&quot; As for the Username and Password, I will enter the username of &quot;admin&quot; and generate a strong password using the LastPass password generator or use the one already provided. This username and password are for accessing the website and should be different from the database username and password, which are only for connecting your WordPress installation to its database. For the email, I&apos;m going to write my email. Please note the username, password, and email somewhere safe before continuing. For the Search Engine Visibility checkbox, I recommend clicking it because you don&apos;t want your website to be indexed by search engines such as Google and Bing before you finish creating all the pages and blog posts that you want to complete first. After entering all the information, we click on the Install WordPress button, and we have finished installing WordPress.&nbsp;</p><br/><h2>Login Into WordPress</h2><p> We can now click on the login button that will take us to our dashboard login page or add the forward slash &quot;wp-admin&quot; to the end of our website domain. I will type my dashboard username and password to access my website. We have successfully logged in. Now that&apos;s how you install WordPress on CPanel. The following steps I recommend are installing specific plugins for things like security and Search Engine Optimization and removing particular plugins that are not helpful. Thank you for watching this tutorial; remember to let me know what next topic you would like to learn. In the description of this video, I will leave the skills I have experience in and can teach you, so please leave a comment to let me know or send me an email. Remember to like this video and subscribe to my channel to get a notification of my newest web development video.&nbsp;</p><br/><p><strong>References</strong></p><p> (1) <a href='https://wordpress.org/about/requirements/' aria-label='WordPress Requirements' target='_blank' rel='noreferrer noopener'>WordPress Requirements</a></p>",
